body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.search-city{grid-area:form;place-self:center;padding-left:10px}.search-city__input{width:250px;margin:5px;padding:7px;border:1px solid #bbb7b7;background-color:#fafafa;font-size:1.5rem;letter-spacing:1px;border-radius:2px}

.result{display:grid;grid-gap:15px;gap:15px;grid-area:results;justify-items:center;margin-left:25px;margin-right:25px;padding-top:20px;border-top:2px solid #627e75}.result__intro{grid-column:1/-1;text-align:center;font-size:1.8rem;color:#226ba3}.result__details{font-size:1.6rem;letter-spacing:1px}.result__parameter{font-weight:bold;color:#226ba3;font-size:1.9rem}@media (min-width: 460px){.result{grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));justify-items:stretch}}

*{margin:0;padding:0;box-sizing:border-box}html,body{font-size:10px}.wrapper{display:grid;max-width:1024px;margin:0 auto;grid-template-rows:60px 70px auto;grid-template-columns:1fr;grid-template-areas:'headline' 'form' 'results'}@media (min-width: 460px){.wrapper{grid-template-columns:1fr minmax(200px, auto);grid-template-areas:'form headline' 'results results';grid-template-rows:80px auto}}.headline{align-self:center;justify-self:center;font-size:2rem;text-transform:uppercase;letter-spacing:1px;color:#226ba3;text-align:center}@media (min-width: 460px){.headline{justify-self:end;padding-right:20px}}

